@import 'ScssHelpers/helpers-all.scss';

.story_hero__in {
	position: relative;
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.vertical_line {
	.story_hero & {
		left: -2.3%;
		top: 50%;
		transform: translateY(-50%);
		height: 100%;

		@include bp(1300px) {
			left: 0;
		}

		@include bp($point_6) {
			top: 0%;
			transform: translateY(0);
			left: -2.3rem;
			height: 100%;
		}
	}

	.story_hero--light_mod & {
		&__line {
			background-color: rgba(33, 33, 33, 0.3);
		}

		&__dot {
			background-color: $black_5;
		}

		&__start::before,
		&__end::before {
			background-color: #f8f8f8;
		}

		@include bp($point_3) {
			display: none;
		}
	}

	.story_hero--dark_mod & {

		&__dot {
			background-color: $white;
		}

		&__line {
			background-color: rgba(255, 255, 255, 0.3);
		}

		&__start::before,
		&__end::before {
			background-color: $black;
		}
	}
}
