@import '../helpers/helpers-all.scss';
@import '../components/vertical-line.scss';

.humanity_creativity {
	position: absolute;
	top: 0;
	left: 0;

	background-color: #f8f8f8;
	height: 100vh;

	opacity: 0;
}

.humanity_creativity__text {
	position: relative;
	margin: 0 auto;
	max-width: 105.3rem;

	font-family: $raleway;
	font-size: 3.6rem;
	font-weight: 700;
	line-height: 140%;
	color: $black_3;
	text-align: center;

	@include bp($point_1) {
		padding: 0 2rem;
		max-width: 70rem;
		font-size: 3rem;
	}

	@include bp($point_4) {
		padding: 0;
		max-width: 50rem;
		font-size: 2.4rem;
	}

	@include bp($point_5) {
		position: relative;
	}

	&::before {
		content: '';
		position: absolute;
		left: -4.2rem;
		top: -1.5rem;
		width: 2.9rem;
		height: 2.9rem;
		border-radius: 50%;
		background-color: $black_5;

		@include bp($point_2) {
			left: -3rem;
			top: -3rem;
		}

		@include bp($point_4) {
			width: 1.3rem;
			height: 1.3rem;
		}

		@include bp($point_5) {
			left: -1.3rem;
			top: -9.7rem;
		}
	}
}
