@import '../helpers/helpers-all.scss';
@import '../components/vertical-line.scss';
.innovation {
	background-color: $black;
	height: 100vh;
	overflow: hidden;
}

.innovation__title {
	margin: 0 auto;
	max-width: 103.8rem;

	font-size: 11.5rem;
	font-weight: 400;
	line-height: 98%;
	color: $white;
	text-align: center;

	@include bp($point_3) {
		padding: 0 2rem;

		font-size: 7.1rem;
	}

	@include bp($point_5) {
		max-width: 32.6rem;

		text-align: start;
	}
}

.innovation__play {
	position: absolute;
	bottom: -2rem;
	left: 50%;
	display: none;

	width: 3.3rem;
	height: 2.3rem;

	cursor: pointer;
	transform: translateX(-50%);

	@include bp($point_5) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	&::before {
		content: '';
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0.6rem 0 0.6rem 1.1rem;
		border-color: transparent transparent transparent $white;
		border-radius: 2px;
		transition: border-color 0.3s ease-in-out;
	}

	&:hover {
		&::before {
			border-color: transparent transparent transparent $yellow;
		}
	}
}
